.main {
    background-color: #ffffff;
    min-height: 60vh;
    display: flex;
    justify-content: center;
    font-size: 16px;
    color: #000000;
}

.form {
    padding: 20px 20px;
    border: 1px solid #ccc;
    box-shadow:0 4px 5px rgba(0, 0, 0, .14);
    height: 30em;
    margin: 5px 0 0 0;
}

.form:hover {
    border: 1px solid #aaa;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, .25);
}

.button {
    text-align: center;
    font-weight: 700;
    color: #ffffff;
    background-color: #f11e22;
    text-transform: uppercase;
    width: 200px;
    padding: 10px;
    font-size: 14px;
    margin: 0 auto;
}

.button:hover,
.button:focus,
.button:active{
    color: #f2f2f2;
    box-shadow:0 4px 5px rgba(0, 0, 0, .14);
}
